import React from 'react';
import { Typography } from '@mui/material';
import { useTranslation } from 'react-i18next';

const HebrewDateDisplay = ({ hebrewDate, fromExodus }) => {
    const { t } = useTranslation();

    return (
        <>
            <Typography variant="h4" component="h1" gutterBottom>
                {t('hebrew_date_title')}
            </Typography>
            <Typography variant="h6" component="div" gutterBottom>
                {t('hebrew_from_creation_date')}: {hebrewDate.toString('he') || t('hebrew_date_loading')}
            </Typography>
            <Typography variant="body1" component="div">
                {t('hebrew_date_from_exodus')}: {fromExodus || t('from_exodus_loading')}
            </Typography>
        </>
    );
};

export default HebrewDateDisplay;
