import React, { useState, useEffect } from 'react';
import { HDate } from '@hebcal/core';
import { Container, Box, Typography, Button } from '@mui/material';
import { v4 as uuidv4 } from 'uuid';
import LanguageSwitcher from './components/LanguageSwitcher';
import HebrewDateDisplay from './components/HebrewDateDisplay';
import EventTimeline from './components/EventTimeline';
import AddEventDialog from './components/AddEventDialog';
import { exodusDate, convertToExodusDiff, formatDuration } from './utils/utils';
import { useTranslation } from 'react-i18next';
import './App.css';

const App = () => {
    const { t } = useTranslation();
    const [hebrewDate, setHebrewDate] = useState('');
    const [fromExodus, setFromExodus] = useState('');
    const [events, setEvents] = useState([]);
    const [open, setOpen] = useState(false);

    useEffect(() => {
        const today = new Date();
        const hd = new HDate(today);
        setHebrewDate(hd.render('he'));

        const years = hd.yy - exodusDate.yy + 1;
        const months = hd.mm;
        const days = hd.dd - exodusDate.dd + 1;
        setFromExodus(formatDuration(days, months, years));

        const storedEvents = localStorage.getItem('events');
        if (storedEvents) {
            setEvents(JSON.parse(storedEvents));
        } else {
            setEvents([{ id: uuidv4(), name: ' ', date: formatDuration(15, 1, "0001") }]);
        }
    }, []);

    const handleClickOpen = () => {
        setOpen(true);
    };

    const handleClose = () => {
        setOpen(false);
    };

    const handleAddEvent = (name, date, isRegularDate) => {
        const eventDate = isRegularDate ? convertToExodusDiff(date) : date;
        const newEvent = { id: uuidv4(), name, date: eventDate };
        const updatedEvents = [...events, newEvent];
        setEvents(updatedEvents);
        localStorage.setItem('events', JSON.stringify(updatedEvents));
    };

    const handleDeleteEvent = (id) => {
        const updatedEvents = events.filter(event => event.id !== id);
        setEvents(updatedEvents);
        localStorage.setItem('events', JSON.stringify(updatedEvents));
    };

    const handleUpdateEvent = (id, name, date) => {
        const updatedEvents = events.map(event => (event.id === id ? { ...event, name, date } : event));
        setEvents(updatedEvents);
        localStorage.setItem('events', JSON.stringify(updatedEvents));
    };

    const handleClearEvents = () => {
        localStorage.removeItem('events');
        setEvents([]);
    };

    return (
        <Container maxWidth="sm" sx={{ textAlign: 'center', marginTop: 4 }}>
            <LanguageSwitcher />
            <Box sx={{ bgcolor: '#fff', padding: 4, borderRadius: 2, boxShadow: 3, marginBottom: 4 }}>
                <HebrewDateDisplay hebrewDate={hebrewDate} fromExodus={fromExodus} />
            </Box>
            <Button variant="contained" color="primary" onClick={handleClickOpen} sx={{ marginRight: 2 }}>
                {t('add_event')}
            </Button>
            <Button variant="contained" color="secondary" onClick={handleClearEvents}>
                {t('clear_events')}
            </Button>
            <AddEventDialog open={open} onClose={handleClose} onAddEvent={handleAddEvent} />
            <Box sx={{ bgcolor: '#fff', padding: 4, borderRadius: 2, boxShadow: 3, marginTop: 4 }}>
                <Typography variant="h5" component="h2" gutterBottom>
                    {t('event_timeline')}
                </Typography>
                <EventTimeline events={events} onDelete={handleDeleteEvent} onUpdate={handleUpdateEvent} />
            </Box>
        </Container>
    );
};

export default App;
