import React, { useState } from 'react';
import { Dialog, DialogActions, DialogContent, DialogTitle, TextField, Button, FormControl, RadioGroup, FormControlLabel, Radio } from '@mui/material';
import { DatePicker } from '@mui/x-date-pickers/DatePicker';
import { LocalizationProvider } from '@mui/x-date-pickers/LocalizationProvider';
import { AdapterMoment } from '@mui/x-date-pickers/AdapterMoment';
import moment from 'moment';
import { useTranslation } from 'react-i18next';

const AddEventDialog = ({ open, onClose, onAddEvent }) => {
    const { t } = useTranslation();
    const [newEventName, setNewEventName] = useState('');
    const [newEventDate, setNewEventDate] = useState(null); // Changed to null for DatePicker
    const [selectedOption, setSelectedOption] = useState('regular');

    const handleOptionChange = (event) => {
        setSelectedOption(event.target.value);
    };

    const handleAddEvent = () => {
        const isRegularDate = selectedOption === 'regular';
        onAddEvent(newEventName, newEventDate, isRegularDate);
        setNewEventName('');
        setNewEventDate(null);
        onClose();
    };

    return (
        <Dialog open={open} onClose={onClose}>
            <DialogTitle>{t('add_event_dialog_title')}</DialogTitle>
            <DialogContent>
                <TextField
                    autoFocus
                    margin="dense"
                    label={t('event_name_label')}
                    fullWidth
                    variant="outlined"
                    value={newEventName}
                    onChange={(e) => setNewEventName(e.target.value)}
                />
                <FormControl component="fieldset" sx={{ marginTop: 2 }}>
                    <RadioGroup
                        aria-label="event-date-options"
                        name="event-date-options"
                        value={selectedOption}
                        onChange={handleOptionChange}
                        row
                    >
                        <FormControlLabel value="regular" control={<Radio />} label={t('regular_date_option')} />
                        <FormControlLabel value="fromExodus" control={<Radio />} label={t('from_exodus_date_option')} />
                    </RadioGroup>
                </FormControl>
                {selectedOption === 'regular' ? (
                    <LocalizationProvider dateAdapter={AdapterMoment}>
                        <DatePicker
                            label={t('event_date_label')}
                            value={newEventDate}
                            onChange={(newValue) => setNewEventDate(newValue)}
                            renderInput={(params) => <TextField {...params} fullWidth />}
                            views={['year', 'month', 'day']}
                            minDate={moment('0000-01-01', 'YYYY-MM-DD')}
                            maxDate={moment('4000-12-31', 'YYYY-MM-DD')}
                        />
                    </LocalizationProvider>
                ) : (
                    <TextField
                        margin="dense"
                        label={t('event_date_label')}
                        type="text"
                        placeholder="DD.MM.YYYY"
                        fullWidth
                        variant="outlined"
                        InputLabelProps={{ shrink: true }}
                        value={newEventDate}
                        onChange={(e) => setNewEventDate(e.target.value)}
                    />
                )}
            </DialogContent>
            <DialogActions>
                <Button onClick={onClose} color="secondary">
                    {t('cancel_button')}
                </Button>
                <Button onClick={handleAddEvent} color="primary">
                    {t('add_button')}
                </Button>
            </DialogActions>
        </Dialog>
    );
};

export default AddEventDialog;
